<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process45"></a>

<p><br></p>
<p><span class="rvts17">战斗要素与元素抵抗</span></p>
<p><img width="597" height="766" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber135.jpg"><span class="rvts18"></span>
<br><span class="rvts21">玩家在推动游戏轮回的时候，需要考虑战斗要素和元素抵抗问题。充分了解以下内容，会使玩家近一步了解战斗中的一些详细内容。</span>
<br><span class="rvts46"></span>
<br><span class="rvts33">1. </span><span class="rvts29">生命值（</span><span class="rvts33">Vitality</span><span class="rvts29">）</span>
<br><span class="rvts21">生命值将会随着玩家等级的提升得到默认的额外提升（即便玩家的体质值未发生变化）。</span>
<br><span class="rvts43"></span>
<br><span class="rvts33">2. </span><span class="rvts29">行动值（</span><span class="rvts33">AP = Action Points</span><span class="rvts29">）</span>
<br><span class="rvts21">行动值决定玩家在战斗过程中可以使用的行动次数。一般情况下，玩家会在战斗开始拥有</span><span class="rvts20">4</span><span class="rvts21">点的初始行动值，并且会在每轮战斗中获得额外的</span><span class="rvts20">4</span><span class="rvts21">点行动值。</span>
<br><span class="rvts21">如果玩家选择略过一轮攻击，那么行动值将被储存下来，最多可储存</span><span class="rvts20">6</span><span class="rvts21">点（除非玩家得到了那个可以增加</span><span class="rvts20">2</span><span class="rvts21">点最大行动值的</span><span class="rvts20"></span><span class="rvts21">孤狼</span><span class="rvts20"></span><span class="rvts21">天赋，也就是说行动值上限提升为</span><span class="rvts20">8</span><span class="rvts21">点）</span>
<br><span class="rvts43"></span>
<br><span class="rvts33">3. </span><span class="rvts29">源力值（</span><span class="rvts33">SP = Source Points</span><span class="rvts29">）</span>
<br><span class="rvts21">源力值只有在一些特定的任务中获得，最大获取上限为</span><span class="rvts20">3</span><span class="rvts21">点。（源力值的涉及内容需要剧情进入到第二章节）</span>
<br><span class="rvts43"></span>
<br><span class="rvts33">4. </span><span class="rvts29">元素抵抗（</span><span class="rvts33">Elemental Resistence</span><span class="rvts29">）</span>
<br><span class="rvts21">元素伤害包含火，水，土，气，毒五种不同的类型。玩家具备这</span><span class="rvts20">5</span><span class="rvts21">种元素的抵抗值。需要说明的是，当玩家的某种元素抵抗值高于</span><span class="rvts20">100%</span><span class="rvts21">时，玩家将完全抵抗该元素，并且会从额外的百分比中获得治疗。</span>
<br><span class="rvts21">举例，假如玩家的火焰抵抗值为</span><span class="rvts20">150%</span><span class="rvts21">，此时受到了敌人的一个</span><span class="rvts20">500</span><span class="rvts21">点伤害的火球攻击，那么玩家将会完全抵抗掉这次火焰攻击伤害，并从中获得额外的</span><span class="rvts20">250</span><span class="rvts21">点生命治疗。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



